@use './mixin/mixin' as *;
@use './setting/setting' as *;
@use './setting/normalize' as *;

body {
  font-family: 'Source Sans Pro', sans-serif, 'ヒラギノ角ゴ ProN', 'Hiragino Kaku Gothic ProN', 'メイリオ', Meiryo, 'ＭＳ Ｐゴシック', 'MS PGothic', sans-serif;
  background-color: #050507;
  color: #fff;
  text-align: center;
  position: relative;
}

/* =====================
  header
===================== */
.header{
  padding: 0 35px 0 30px;
  position: fixed;
  left: 0;
  right: 0;
  z-index: 2;
}
.header_inner {
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s linear,visibility 0.3s linear;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.is-showHeader {
  opacity: 1;
  visibility: visible;
}
.is-shouMenu {
  .menu_wrapper {
    top: 0;
  }
  .menu_icon_parts {
    &:nth-of-type(1) {
      transform: rotate(-45deg) translate(-1px, -1px);
    }
    &:nth-of-type(2) {
      transform: rotate(45deg) translate(7px, 9px);
    }
    &:nth-of-type(3) {
      transform: rotate(45deg) translate(0px, 2px);
    }
  }
}

.header_logo{
  width: 140px;
  margin-top: 21px;
}

.menu_icon{
  width: 30px;
  height: 20px;
  position: relative;
  top: 12px;
  &:hover {
    cursor: pointer;
  }
}

.menu_icon_parts {
  background-color: #fff;
  height: 1px;
  width: 100%;
  position: absolute;
  top: 10px;
  left: 0;
  transform-origin: right;
  transition: transform 0.6s ease-in-out;
  &:nth-of-type(1) {
    top: 0;
  }
  &:nth-of-type(3) {
    top: 20px;
  }
}

.menu_wrapper {
  position: absolute;
  top: -100vh;
  background-color: #050507;
  z-index: -1;
  left: 0;
  right: 0;
  height: 100vh;
  text-align: center;
  transition: top 0.6s ease-in-out;
}

.menu_list{
  margin-top: 90px;
}

.menu_item{
  font-size: 16px;
  & + .menu_item {
    margin-top: 20px;
  }
}

.menu_link {
  color: #fff;
}
/* =====================
  END header
===================== */

/* =====================
  mainVisual
===================== */
.mainVisual{
  display: flex;
  justify-content: center;
  overflow: hidden;
}

.mainVisual_image{
  width: 100%;
  height: 100vh;
  object-fit: cover;
  @include pc {
    width: 33.3333%;
  }
}
/* =====================
  END mainVisual
===================== */

.exhibition_title {
  margin-top: 50px;
  font-size: 48px;
  text-align: center;
  font-weight: normal;
  line-height: lh(60, 48);
  @include pc {
    margin-top: 100px;
    font-size: 100px;
  }
}

.common_wrapper {
  padding: 0 30px;
}

.common_title {
  font-size: 32px;
  font-weight: normal;
  border-bottom: 1px solid #fff;
  display: inline-block;
  @include pc {
    font-size: 60px;
  }
}

.fadeIn {
  opacity: 0;
  transition: opacity 2s ease, transform 2s ease;
  transform: translateY(50%);
}

.add-fadeIn {
  opacity: 1;
  transform: translateY(0);
}

.background {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  opacity: 0;
  transition: opacity 0.5s linear;
  background-image: url(../img/background.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  &.is-showbackground {
    opacity: 1;
  }
}

/* =====================
  infomation
===================== */
.infomation {
  margin-top: 50px;
  @include pc {
    margin-top: 100px;
  }
}
.date {
  margin-top: 45px;
}
.date_description {
  font-size: 16px;
  line-height: lh(24, 16);
  @include pc {
    font-size: 18px;
  }
  .date_description_english {
    @include pc {
      font-size: 24px;
    }
  }
}
.place {
  font-size: 32px;
  font-weight: bold;
  margin-top: 20px;
  font-size: 40px;
  & + .date {
    margin-top: 20px;
  }
}
/* =====================
  END infomation
===================== */

/* =====================
  gallery
===================== */
.gallery {
  margin-top: 80px;
  @include pc {
    margin: 140px auto 0;
    max-width: 800px;
  }
}
.gallery_list {
  margin-top: 45px;
}
.gallery_item {
  margin-top: 30px;
  @include pc {
    width: calc(100% / 2 - 15px);
  }
  &:nth-child(2n) {
    margin-left: auto;
  }
  & + .gallery_item {
    @include pc {
      margin-top: 60px;
    }
  }
}
/* =====================
  END gallery
===================== */

/* =====================
  access
===================== */
.access {
  margin-top: 280px;
  position: relative;
  z-index: 1;
  @include pc {
    margin-top: 480px;
  }
}

.access_hall{
  margin-top: 45px;
  font-weight: bold;
  font-size: 28px;
}

.access_description {
  font-size: 16px;
  margin-top: 20px;
  line-height: lh(23.2, 16);
  display: block;
}

.access_map {
  color: #fff;
  border: 1px solid #fff;
  width: 270px;
  display: block;
  margin: 30px auto 0;
  padding: 15px 0;
  background-color: transparent;
  transition: background-color 0.2s linear;
  &:hover {
    background-color: #000;
  }
}
/* =====================
  END access
===================== */

/* =====================
  contact
===================== */
.contact{
  margin: 280px 0 200px;
  position: relative;
  z-index: 1;
  @include pc {
    margin-top: 480px;
  }
}

.contact_button_wrapper {
  margin-top: 45px;
}

.contact_button {
  color: #fff;
  display: inline-block;
  border: 1px solid #fff;
  width: 100%;
  padding: 30px 0;
  transition: opacity 0.3s ease;
  @include pc {
    display: block;
    width: 400px;
    margin: auto;
    position: relative;
  }
  & + .contact_button {
    margin-top: 20px;
  }
  &::before,
  &::after {
    @include pc {
      content: '';
      position: absolute;
      right: -40px;
      height: 1px;
      background: #fff;
      transition: right 0.3s ease;
    }
  }

  &::before {
    @include pc {
      bottom: 30px;
      width: 120px;
    }
  }
  &::after {
    @include pc {
      bottom: 34px;
      width: 15px;
      transform: rotate(30deg);
    }
  }
  &:hover {
    opacity: 0.6;
  }
  &:hover::before,
  &:hover::after {
    right: -50px;
  }
}
/* =====================
  END contact
===================== */

/* =====================
  following
===================== */
.following_list{
  transform: rotate(180deg);
  position: fixed;
  bottom: 200px;
  right: -48px;
  background-color: #050507;
  transition: right 0.3s ease-in-out;
  &.is-showFollowing {
    right: 0;
  }
}

.following_item {
  writing-mode: vertical-rl;
  border: 1px solid #fff;
  border-left: none;
  & + .following_item {
    border-top: none;
  }
}

.following_link {
  display: block;
  color: #fff;
  padding: 40px 15px;
  font-size: 14px;
  transition: opacity 0.2s linear;
  &:hover {
    opacity: 0.6;
  }
}

/* =====================
  END following
===================== */

/* =====================
  footer
===================== */
.footer{
  border-top: 1px solid #fff;
  padding: 60px 30px;
  text-align: left;
  @include pc {
    padding: 80px 30px;
  }
}

.shop_info{
  font-size: 12px;
  line-height: lh(19.2, 12);
}

.footer_wrapper {
  @include pc {
    display: flex;
    justify-content: space-between;
  }
}

.sns_list{
  margin-top: 30px;
  display: flex;
}

.sns_item + .sns_item{
  margin-left: 15px;
}

.sns_link{
  color: #fff;
  font-size: 12px;
  line-height: lh(25.6, 12);
}

.copyright_list{
  margin-top: 30px;
  display: flex;
  align-items: center;
}

.copyright_item{
  font-size: 12px;
  & + .copyright_item {
    margin-left: 30px;
  }
}

.privacypolicy{
  color: #fff;
  font-size: 12px;
}
/* =====================
  END footer
===================== */